/* bug fix */
.navbar-fixed {
  z-index: 9999;
  position: absolute;
}


/* ---------------------------------- */
/* navbar config */
/* ---------------------------------- */


/* nav color */
header nav {
  background-color: transparent !important;
  box-shadow: none;
}

/* muda a cor da nav ao descer */
.nav-down {
  background-color: #2196f3 !important;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

/* padding */
header nav {
  padding-bottom: 30px;
}

/* ajusta o logo */
.brand-logo {
  line-height: 80px;
}

@media only screen and (max-width: 600px) {
  .brand-logo {
    line-height: 70px;
  }

}

/* aumenta a width dos dropdowns */
.dropdown-content {
  width: 250px !important;
}

/* dropdown redes sociais icons size */
nav .dropdown-content i {
  font-size: 40px;
  color: #2196f3 !important;
}

/*  redes sociais dropmenu config */
.dropdown-item span {
  position: relative;
  top: -10px;
  left: +3px;
}



/* ---------------------------------- */
/* HEADER VIDEO */
/* ---------------------------------- */

/* container */
.header-video-container {
  width: 100%;
  height: 100vh;
}

/* mascara azul transparente */
.mask {
  top: 0px;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #2196f3ea !important;
}

/* slogan align */
.slogan {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
}
/* config para nao sair fora da view */
.slogan img {
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}


/* video config */
.bloc-video {
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  z-index: 0;
}
.video-bg-container, .bloc-video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: -50%;
  overflow: hidden;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
}
header video {
  display: inline-block;
  vertical-align: baseline;
  object-fit: contain;
}

/* header img (mobile)*/
.header-img img {
  width: 100% !important;
  height: 100vh !important;
  object-fit: cover;
}
